section.banner {
  display: grid;
  grid-template-columns: 50% 5% 45%;
  grid-template-rows: 25% 25% 25% 25%;
  background-image: url("../images/Hero_2000w.webp");
  background-size: cover;
  background-position: center;
  height: 80vh; }
  section.banner span.bannerInfo {
    grid-column: 3;
    grid-row: 2;
    color: white;
    font-size: 2.5rem; }
  @media (max-width: 1250px) {
    section.banner span.bannerInfo {
      font-size: 2rem; } }
  @media (max-width: 1050px) {
    section.banner {
      height: 50vh;
      background-position-x: 40%; }
      section.banner span.bannerInfo {
        font-size: 1.5rem; } }
  @media (max-width: 767px) {
    section.banner {
      left: 40%;
      background-position: center;
      background-image: url("../images/banner_2000w.jpeg");
      display: flex;
      justify-content: flex-start; }
      section.banner span.bannerInfo {
        width: 100%;
        font-size: 0.75rem;
        margin-left: 0.75rem;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: 10% 10% 80%; }
        section.banner span.bannerInfo h1 {
          margin-top: 0.5rem; }
        section.banner span.bannerInfo p.email {
          margin-top: 0.5rem;
          grid-area: 1 / 2 / 3 / 2;
          text-align: right;
          margin-right: 0.75rem; }
        section.banner span.bannerInfo p.occupation {
          grid-column: 1;
          grid-row: 2;
          margin-top: 0; } }

section.tech {
  padding: 2rem 0;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #5FAD56; }
  section.tech h2 {
    text-align: center;
    margin: 0 0 1rem 0;
    width: 100%;
    color: white; }
  section.tech div {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly; }
    section.tech div article {
      width: 30%;
      padding-bottom: 1rem;
      box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
      background-color: white;
      border-radius: 0.5rem;
      text-align: center; }
      section.tech div article h3 {
        margin-top: 0.5rem; }
      section.tech div article ul {
        list-style: none; }
        section.tech div article ul li {
          display: inline-block;
          width: 50%;
          margin: 0.25rem 0; }
  @media (max-width: 800px) {
    section.tech {
      flex-direction: column; }
      section.tech h2 {
        margin-bottom: 0; }
      section.tech div {
        flex-direction: column;
        align-items: center;
        justify-content: space-between; }
        section.tech div article {
          width: 90%;
          margin: 1rem 0; }
          section.tech div article ul li {
            display: inline-block;
            width: 50%;
            margin: 0.25rem 0; }
          section.tech div article h3 {
            margin-top: 0.5rem; }
          section.tech div article:last-child {
            margin: 0 0 1.5rem 0; } }

section.experience {
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #F2C14E; }
  section.experience h2 {
    margin-bottom: 1rem;
    color: white; }
  section.experience .experienceItems {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly; }
  section.experience .experienceItem {
    width: 30%;
    text-align: center;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
    background-color: white;
    border-radius: 0.5rem; }
    section.experience .experienceItem p {
      margin: 1rem 2rem;
      text-align: left; }
    section.experience .experienceItem h3 {
      margin-top: 0.5rem; }
  @media (max-width: 1100px) and (min-width: 767px) {
    section.experience .experienceItems {
      flex-direction: column; }
    section.experience .experienceItem {
      grid-template-rows: 15% 85%;
      width: 90%;
      margin-left: 5%;
      margin-bottom: 1rem;
      height: 20vh; }
      section.experience .experienceItem p {
        margin: 1rem 1rem; } }
  @media (max-width: 767px) {
    section.experience .experienceItems {
      flex-direction: column; }
    section.experience .experienceItem {
      width: 90%;
      margin-left: 5%;
      margin-bottom: 1rem;
      height: 30vh; }
      section.experience .experienceItem p {
        margin: 1rem 1rem; } }
  @media (max-width: 350px) {
