section.about {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 2rem 0;
  background-color: #FF5964; }
  section.about h2 {
    text-align: center;
    margin-bottom: 1rem;
    color: white;
    width: 100%; }
  section.about .background {
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
    background-color: white;
    border-radius: 0.5rem;
    padding: 1rem;
    width: 92.5%; }
    section.about .background h3 {
      margin-bottom: 0.5rem; }
    @media (max-width: 800px) {
      section.about .background {
        width: 82.5%; }
        section.about .background h3 {
          text-align: center; } }
  section.about .hobbies {
