    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly; }
    section.about .hobbies h3 {
      width: 100%;
      margin-bottom: 1rem; }
    section.about .hobbies .slide {
      width: 90%;
      display: flex;
      flex-direction: row;
      margin-bottom: 0.5rem; }
      section.about .hobbies .slide img {
        width: 10rem;
