        flex-direction: column;
        align-items: center;
        width: 82.5%; }
        section.about .hobbies h3 {
          text-align: center; }
        section.about .hobbies .slide {
          flex-direction: column;
          width: 90%;
          border: none; }
          section.about .hobbies .slide img {
