        section.about .hobbies .slide:nth-child(even) picture, section.about .hobbies .slide:nth-child(odd) picture {
          order: 0; }
        section.about .hobbies .slide:nth-child(even) span, section.about .hobbies .slide:nth-child(odd) span {
          margin: 1rem 0; } }
    @media (max-width: 1000px) and (min-width: 767px) {
